body {
  font-size: 12pt;
}

.Site-content {
  -webkit-box-flex: 1;
  -ms-flex: 1 0 auto;
  flex: 1 0 auto;
  width: 100%;
}

@media (min-height: 56.25em) {
  .Site-content {
    -webkit-box-flex: 0.5;
    -ms-flex: 0.5 0 auto;
    flex: 0.5 0 auto;
  }
}

.u-container,
.u-separator,
.Nav {
  max-width: 800px;
  max-width: 50rem;
  margin: 0 auto;
  padding: 0 1.8em;
}

.disable-dbl-tap-zoom {
  touch-action: manipulation;
}
